import { IFeedOrdersMessage } from "../../utils/types";
import { padOrderNumber } from "../../utils/utils";
import { TSocketMiddlewareActions, WS_CONNECTION_CLOSED, WS_CONNECTION_ERROR, WS_CONNECTION_START, WS_CONNECTION_SUCCESS, WS_GET_MESSAGE } from "../actions/socket-middleware";

interface IWsState {
  wsConnected: boolean;
  request: boolean;
  data: IFeedOrdersMessage<string>;

  error?: Event;
};

export const initialWsState: IWsState = {
  wsConnected: false,
  request: false,
  data: {
    success: false,
    orders: [],
    total: 0,
    totalToday: 0
  }
};
//    WebSocket
export const wsReducer = (state = initialWsState, action: TSocketMiddlewareActions): IWsState => {
  switch (action.type) {
    case WS_CONNECTION_START:
      return {
        ...state,
        request: true,
      };
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        request: false,
        wsConnected: true
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        request: false,
        error: action.payload,
        wsConnected: false
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false
      };

    //      WS_GET_MESSAGE
    //  ,     
    //  messages  ,    
    case WS_GET_MESSAGE: {
      const message: IFeedOrdersMessage<number> = JSON.parse(action.payload);
      return {
        ...state,
        error: undefined,
        data: {
          ...message,
          orders: message.orders?.map(o => ({ ...o, number: padOrderNumber(o.number) })) ?? []
        }
      };
    }
    default:
      return state;
  }
}; 
