import { FC } from 'react';
import styles from './counter.module.css';
//   ,    
export const Counter: FC<{ count: number | undefined }> = ({ count }) => {
    if (!count || count < 1) {
        return null;
    }
    return (
        <div className={styles.main}>
            <p className='text text_type_main-small'>{count}</p>
        </div>
    );
}